@import "../../style/index";

.teamItem {
  width: 170px;
  margin-bottom: 40px;

  img {
    margin-bottom: 1.4rem;
  }

  p {
    text-align: left;
    margin-bottom: 5px;
    font-size: 16px;
    &:first-of-type {
      color: $text-main;
      font-size: 22px;
    }
  }
}

@media (max-width: 575.98px) {
  .teamItem {
    width: 138px;
    margin-bottom: 50px;

    img {
      margin-bottom: 0.6rem;
    }
    p {
      text-align: left;
      margin-bottom: 5px;
      font-size: 12px;
      &:first-of-type {
        color: $text-main;
        font-size: 18px;
        margin-bottom: 0;
      }
    }
  }
}
